import React, { useState } from "react";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormLabel from "@material-ui/core/FormLabel";
import Select from "@material-ui/core/Select";
import Radio from "@material-ui/core/Radio";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/styles";
import theme from "../theme";
import { ThemeProvider } from "@material-ui/styles";
import Error from "./Error"
import { calcularMarca, obtenerDiferenciaYear, obtenerPlan } from '../helpers'
import PropTypes from "prop-types"
const useStyles = makeStyles({
  formStyles: {
    width: "50%",
    marginTop: "10px",
    display: "flex",
    flexDirection: "column",
    alignItems:"center"
  },
  selectStyles: {
    width: "100%",
  },
  selectContainer: {
    marginTop: "10px",
    width:"100%"
  },
  letter: {
    color: "rgba(0, 0, 0, 0.54)",
    padding: "0",
    fontSize: "1rem",
    fontFamily: "Roboto",
    fontWeight: "400",
    lineHeight: "1",
    letterSpacing: "0.00938em",
  },
  button: {
    width: "100%",
  },

});

const Form = ({guardarResumen, guardarCargando}) => {
  const classes = useStyles();
  const [datos, setDatos] = useState({
    marca: "",
    year: "",
    tipoPlan: "",
  });
  const [error, setError] = useState(false)

  const { marca, year, tipoPlan } = datos;

  const handleData = (e)=>{
    setDatos({
        ...datos,
        [e.target.name]: e.target.value,
    })
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    if(marca.trim() === '' || year.trim() === '' || tipoPlan.trim() === ''){
        setError(true)
        return
    }
    setError(false)

    // Una base de 2000
    const base = 2000;

    // Obtener la diferencia de años
    const diferencia = obtenerDiferenciaYear(year)

    // Por cada año hay que restar el 3%

    let resultado = base - (((diferencia * 3) * base) / 100)

    // Incrementamos segun la marca
    const incrementoMarca = calcularMarca(marca)

    resultado = resultado*incrementoMarca

    // Incrementamos segun el plan
    const incrementoPlan = obtenerPlan(tipoPlan)

    resultado = parseFloat(resultado*incrementoPlan).toFixed(2)
   
    // Cargamos el spinner
    guardarCargando(true)

    setTimeout(()=>{
      // Eliminar Spinner
      guardarCargando(false)
      // Enviar datos al componente principal
      guardarResumen({
        cotizacion: Number(resultado),
        datos
      })
    },2500)

    // Enviamos al componente principal
   
    
  };

  return (
    <ThemeProvider theme={theme}>
      <form className={classes.formStyles} onSubmit={handleSubmit}>
        {
            error ? <Error /> : null
        }
        <div className={classes.selectContainer}>
          <InputLabel id="marcaAutoLabel">Marca</InputLabel>
          <Select
            className={classes.selectStyles}
            labelId="marcaAutoLabel"
            id="marcaAuto"
            name="marca"
            color="secondary"
            value={marca}
            onChange={handleData}
                
          >
            <MenuItem value="Americano">Americano</MenuItem>
            <MenuItem value="Europeo">Europeo</MenuItem>
            <MenuItem value="Asiático">Asiático</MenuItem>
          </Select>
        </div>

        <div className={classes.selectContainer}>
          <InputLabel id="yearLabel">Año</InputLabel>
          <Select
            className={classes.selectStyles}
            labelId="yearLabel"
            id="year"
            name="year"
            color="secondary"
            value={year}
            onChange={handleData}
                
          >
            <MenuItem value="2021">2021</MenuItem>
            <MenuItem value="2020">2020</MenuItem>
            <MenuItem value="2019">2019</MenuItem>
            <MenuItem value="2018">2018</MenuItem>
            <MenuItem value="2017">2017</MenuItem>
            <MenuItem value="2016">2016</MenuItem>
            <MenuItem value="2015">2015</MenuItem>
            <MenuItem value="2014">2014</MenuItem>
          </Select>
        </div>
        <div className={classes.selectContainer}>
          <FormLabel>Tipo de plan</FormLabel>
          <br />
          <label className={classes.letter} htmlFor="Completo">
            Básico
          </label>
          <Radio
            name="tipoPlan"
            value="Básico"
            checked={tipoPlan === "Básico"}
            onChange={handleData}
          />
          <label className={classes.letter} htmlFor="Completo">
            Completo
          </label>
          <Radio
            name="tipoPlan"
            color="secondary"
            value="Completo"
            checked={tipoPlan === "Completo"}
            onChange={handleData}
                
          />
        </div>
        <Button
          type="submit"
          className={classes.button}
          variant="contained"
          color="secondary"
        >
          Cotizar
        </Button>
      </form>
    </ThemeProvider>
  );
};

Form.propTypes = {
  guardarResumen: PropTypes.func.isRequired,
  guardarCargando: PropTypes.func.isRequired,
}
export default Form;
