import React from "react";
import { Typography } from "@material-ui/core";
import { ListGroup, ListGroupItem } from "reactstrap";
import "bootstrap/dist/css/bootstrap.min.css"
import { makeStyles } from '@material-ui/styles';
import PropTypes from "prop-types"

const useStyles = makeStyles({
  letter: {
    color: "rgba(0, 0, 0, 0.54)",
    padding: "0",
    fontSize: "1rem",
    fontFamily: "Roboto",
    fontWeight: "400",
    lineHeight: "1",
    letterSpacing: "0.00938em",
    transition: "all 5000ms"
  },
})
const Resumen = ({ datos }) => {
  const { marca, year, tipoPlan } = datos;
  const classes = useStyles()
  const typoClass = `${classes.letter} mt-4 mb-2 h6`
  if (marca === "" || year === "" || tipoPlan === "") return null;


  return (
    <div>
      <Typography className={typoClass}>RESUMEN DE COTIZACIÓN</Typography>
      <ListGroup className="mb-2">
        <ListGroupItem ><strong>Marca:</strong> {marca}</ListGroupItem>
        <ListGroupItem><strong>Año del auto:</strong> {year}</ListGroupItem>
        <ListGroupItem><strong>Plan:</strong> {tipoPlan}</ListGroupItem>
      </ListGroup>
    </div>
  );
};

Resumen.propTypes = {
  datos: PropTypes.object.isRequired
}
export default Resumen;
