import React from "react";
import { makeStyles } from "@material-ui/styles";
import "./Resultado.css";
import PropTypes from "prop-types"

const useStyles = makeStyles({
  letter: {
    color: "white",
    padding: "0",
    fontSize: "1rem",
    fontFamily: "Roboto",
    fontWeight: "400",
    lineHeight: "1",
    letterSpacing: "0.00938em",
    textAlign: "center",
    transition: "all 500ms",
    
  },
  mensaje: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "#2196f3",
    padding: "20px",
    width:"100%",
    height:"60px",
    borderRadius: "2px",
    position: "relative",
    transition: "all 500ms"
  },
});
const Resultado = ({ cotizacion }) => {
  const classes = useStyles();
  return (
    <div className="mt-2 h6">
      {cotizacion === 0 ? (
        <div className={classes.mensaje}>
          <span className={classes.letter}>Ingrese marca, año y plan</span>
        </div>
      ) : (
        <div className={classes.mensaje}>
          <span className={classes.letter}>El total es de: ${cotizacion}</span>

        </div>
      )}
    </div>
  );
};

Resultado.propTypes = {
  cotizacion: PropTypes.number.isRequired
}
export default Resultado;
